.array-container {
    position: absolute;
    left: 100px;
    top: 50px;
}

.array-bar {
    width: 2px;
    background-color: rgb(68, 0, 194);
    display: inline-block;
    margin: 0 1px;
}

button {
    background-color: rgb(68, 0, 194);
    color: white;
    border: none;
    padding: 10px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin-left: 50px;
}